import "./Copy.css";
import React, { useState, useEffect } from 'react';
import axios from 'axios';



function Copy() {
  const [jsonData, setJsonData] = useState(null);

  function corp(no){
    window.open("http://www.ftc.go.kr/bizCommPop.do?wrkr_no="+no,"","width=500 height=500");
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('http://whdwns268.cafe24.com/portfolio1/admin/config_form_react.do');
        setJsonData(response.data);
        //console.log(response);
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      }
    };

    fetchData();
  }, []);

  if (!jsonData) {
    return <div>Loading...</div>;
  }
  return (
    <div className="copy_full">
      <div className="copy_full_title">
        <span className="copy_inner_title">
          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li>/</li>
          </ul>
          <ul>
            <li><img src="./mall_img/ico/customer_center.gif" /></li>
            <li>{jsonData[3]}</li>
          </ul>
        </span>
        <span className="copy_inner_index">
          <div>
            <table className="copy_table">
              <tbody>
                <tr>
                  <td>:{jsonData[0]} </td>
                  <td>:{jsonData[1]} </td>
                  <td>:{jsonData[2]} <a className="cupoint" onClick={() => corp('1188120586')}>[]</a> </td>
                </tr>
                <tr>
                  <td colSpan={2}>:{jsonData[4]} </td>
                  <td> :{jsonData[5]}</td>
                </tr>
                <tr>
                  <td colSpan={2}>:{jsonData[6]}</td>
                  <td>  E-mail:{jsonData[7]}</td>
                </tr>
                <tr>
                <td colSpan={4}>    All Rights reserved</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div>
            <ul>
              <li><img src="./mall_img/ico/bank_info.gif" className="imgbank" /></li>
              <li> :{jsonData[8]}</li>
              <li>:{jsonData[9]}</li>
            </ul>
          </div>
        </span>
      </div>
    </div>
  );
}

export default Copy;
